<template>
    <div>
        <div class="left-menu-tab new_tab nosrcoll">
            <div class="col-md-12 p-0 ">
                <div class="theme-primary partition">
                    <span class="p-name text-white">Report Listing</span>
                </div>
            </div>
            <loader :is-visible="isLoading"></loader>
            <div class="pt-2 mb-1 w-100 d-none d-lg-block">
                <div class="form-group">
                    <input v-bind:type="'text'" class="form-control search" v-on:keyup="ListFiltering"
                        :placeholder="'Search By Name'" id="SearhField" />
                </div>
            </div>
            <PerfectScrollbar>
                <div class="mt-2 mCustomScrollbar _mCS_4 mCS_no_scrollbar " id="divscrollsetuptabs"
                    style="max-height:calc(100vh - 2px)">
                    <ul class="nav nav-pills flex-column left-tab responivecustomtab lefttabiconfix" id="upper"
                        role="tablist">
                        <li style="display: list-item;" v-for="(item, itemIndex) in ReportData" :key="itemIndex">
                            <a class="nav-link left-menu text-truncate" style="width: 80%;"
                                v-on:click="GetReportList(item)">
                                <i class="fa fa-file-text-o mr-2" aria-hidden="true"></i>{{ item.REPORTNAME + " (" + item.FOLDER_NAME + ")" }}
                            </a>
                        </li>
                    </ul>
                </div>
            </PerfectScrollbar>
        </div>
        <div>
            <ViewCustomReport v-if="IsReportListing" :key="componentKey" :ReportListParameters="ReportListParameters">
            </ViewCustomReport>
        </div>
    </div>
</template>
<script>
import ViewCustomReport from './ViewCustomReport.vue';
import ReportDataService from "@/services/DataService";
export default {
    components: {
        ViewCustomReport
    },
    data() {
        return {
            isLoading: false,
            PageSize: 10,
            PageNumber: 1,
            isPaged: 1,
            SortBy: '',
            SortExp: '',
            searchcondition: '',
            ReportData: [],
            ReportListParameters: {},
            IsReportListing: false,
            GetDeafultReportData: [],
            count: 0,
            componentKey: 0
        }
    },
    async created() {
        var vm = this;
        await vm.FetchData();
        if (vm.GetDeafultReportData != [] && vm.GetDeafultReportData != undefined) {
            vm.GetReportList(vm.GetDeafultReportData);
            vm.count += 1;
        }
    },
    methods: {
        async FetchData() {
            var vm = this;
            const params = `searchCondition=${vm.searchcondition}&is_paged=${vm.isPaged}&sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}`
            await ReportDataService.CustomReportListing(params)
                .then(function (response) {
                    if (response.data != "" && response.data != null) {
                        if (response.data.data.length > 0) {
                            vm.ReportData = response.data.data;
                            vm.GetDeafultReportData = vm.ReportData[0];
                            vm.noRecord = false;
                        } else {
                            vm.noRecord = true;
                            vm.ReportData = [];
                        }
                    }
                    else {
                        vm.noRecord = true;
                        vm.ReportData = [];
                    }
                    setTimeout(function () {
                        vm.ResponsiveDataTable('tablelistingdata')
                    }, 500);
                    setTimeout(function () {
                        vm.SetScrolbar('dark-thick', '', 'customScrollForLeftBar')
                    }, 1000);
                    vm.isLoading = false;
                });
        },
        GetReportList(data) {
            var vm = this;
            vm.isLoading = true;
            vm.IsReportListing = true;
            if (vm.count > 0) {
                vm.componentKey += 1;
            }
            vm.ReportListParameters = {
                ReportID: data.REPORT_ID,
                ACCESS: data.ACCESS
            }
            vm.isLoading = false;
        },
        ListFiltering: function () {
            // Declare variables
            var input, filter, ul, li, a, i, txtValue;
            input = document.getElementById('SearhField');
            filter = input.value.toUpperCase();
            ul = document.getElementById("upper");
            li = ul.getElementsByTagName('li');

            // Loop through all list items, and hide those who don't match the search query
            for (i = 0; i < li.length; i++) {
                a = li[i].getElementsByTagName("a")[0];
                txtValue = a.textContent || a.innerText;
                if (txtValue.toUpperCase().indexOf(filter) > -1) {
                    li[i].style.display = "";
                } else {
                    li[i].style.display = "none";
                }
            }
        }
    }
}
</script>
<style scoped>
.ps {
    height: 650px;
}
</style>